}

.box{
    padding: 1.5rem;
    
}

.box1 {
    background-image: url(/react-project-flights/static/media/airline.4a86379a.jpg);
}

.box2 {
    background-image: url(/react-project-flights/static/media/region.aaee426d.jpg);
}
#dashboard {
    position: absolute;
    top: 20px;
    left: 20px;
    background-color: #003459;
    color: #fff;
    padding-left: 20px;
    padding-right: 20px;
}

.button{
    margin-top: auto;
    background: #003459;
    color: #fff;
}

a.link{
    color: #3333FF;
}


.signupLoginContainer {
    margin: auto;
    width: 50%;
}

.signupLoginContainer h1 {
    font-size: 2.5rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
    color: #003459;
}

.login {
    float: right;
    margin-right: 0.5rem;
}

.signup {
    float: right;
    margin-left: 0.5rem;
    margin-right: 0;
    background-color: #003459 !important;
}

.logout {
    margin: auto;
    margin-top: 3rem;
    width: 50%;
}

.logout p {
    margin-left: 1rem;
}

.logout button {
    margin: auto;
    width: 100%;
    background-color: hotpink;
}

.dropdown button {
    margin: auto;
    width: 100%;
}

.show {
    margin: auto;
    width: 100%;
}
.navbar,
.navbar-default {
    background-image: linear-gradient(to bottom,#003459 0,#003459 100%) !important; 
    position: fixed;
    /* override background image gradient w/ flat color */
}
.dropdown-item:hover{
    background-color: #003459;
    color: rgba(255,255,255,.5);
}
nav {
